import "./checkout.css";
import Navbar from "../Components/Navbar/Navbar";
import ProductImg from '../../../assets/product.png';
import Checkoutimg from "../../../assets/checkout.png";
import Qr from "../../../assets/qr.png";
import { FormControl, RadioGroup, FormControlLabel, Radio, Checkbox } from '@mui/material';

import { BrowserRouter as Router, Link } from "react-router-dom";
import Footer from "../../landing_page/Footer";
import $ from 'jquery';
import "jquery-ui-dist/jquery-ui";
import {useEffect} from "react";



function Newcheckout() {
    const productImg = ProductImg;
    useEffect ((useEffect)=>{
        $('#onlineradio').click(function(){
            $('.InstructionsOnline').show();
            $('.InstructionsTransfer').hide();
            $('#CheckOutPayNowBTNTrans').hide();
            $('#CheckOutPayNowBTNOnline').show();
        })
        $('#transferradio').click(function(){
            $('.InstructionsOnline').hide();
            $('.InstructionsTransfer').show();
            $('#CheckOutPayNowBTNTrans').show();
            $('#CheckOutPayNowBTNOnline').hide();
        })
    },[]);
  return (
    
    <>
    
      <section className="place-sec-1">
        <Navbar />
        <h1 className="checkout_heading">Select Payment Method</h1>
        <div className="checkout-container">
            <div className="checkout-container-wraper">
            <div className="lt-container">
                <div className="mb-4">
                    <FormControl className="d_flex">
                        <RadioGroup className="d_flex flex_row" defaultValue="Payment Through Online" aria-labelledby="demo-radio-buttons-group-label"  name="radio-buttons-group" >
                            <button className="checkout-btn"><FormControlLabel value="Payment Through Online" id="onlineradio" control={<Radio />} label="Payment Through Online" /></button>
                            <button className="checkout-btn"><FormControlLabel value="Check /DD/ Cash"  id="transferradio" control={<Radio />} label="Check /DD/ Bank Transfer" /></button> 
                        </RadioGroup>
                    </FormControl>
                </div>
                <div className="rk-wallet-sec d-flex">
                    <div className=" aic">
                    <FormControlLabel control={<Checkbox  />} label="RK Credit" />
                    </div>
                    <div className="ms-auto">
                        <p className="amount">20,000</p>
                        <p className="mt-0 text-gray text-end">Balance</p>
                    </div>
                </div>
                <div className="">
                    <h3>Instructions :</h3>
                    <ul className="instr-list InstructionsOnline">
                        <li className="">If you want to debit amount from your <span>RK wallet</span>  so click shown above check box</li>
                        <li className="">Then Pay from pay now button shown below </li>
                        <li className="">Then Pay from pay now button shown below </li>
                        <li className="">If you want to debit amount from your <span>RK wallet</span> so click shown above check box</li>
                    </ul>
                    <ul className="instr-list InstructionsTransfer">
                        <li className="">If you want to debit amount from your <span>RK wallet</span>so click shown above check box</li>
                        <li className="">You can make the payment with the help of the information given here</li>
                        <li className="">Account Name : Investopher research services private limited<br />
Account No. : 50200054325020<br />
IFSC code : HDFC0003886<br />
Branch : LIG SQUARE INDORE 452010<br /></li>
                        <li className="">After clicking on the button of Pay Now, Thank You window will open up on your screen, if you have any document or any reference number put it there</li>
                    </ul>
                </div>
                <button id="CheckOutPayNowBTNTrans" className="btn pay-btn mt-4 CheckOutPayNowBTN"><Link to='/done2'>Pay Now</Link></button>
                <button id="CheckOutPayNowBTNOnline" className="btn pay-btn mt-4 CheckOutPayNowBTN"><Link to='/done'>Pay Now</Link></button>
            </div>
            <div className="rt-container">
                <div className="product_card">
                    <h3 className="checkout_product_card_heading">Product Info</h3>
                    <div className="d-flex">
                        <div className="checkout_product_img_container">
                            <div className="checkout_product_img">
                                <img src={`${productImg}`} alt="" className="" />
                            </div>
                        </div>
                        <div className="checkout_product_details">
                            <h4 className="mt-1">Camlin Engineers Blue Pastes 20ml</h4>
                            <div className="d-flex">
                                <div className="">
                                    <p className="">Burhani suppliers</p>
                                    <p className="">Qty : 50</p>
                                </div>
                                <div className="ms-auto">
                                    <p className="">Chennai</p>
                                </div>
                            </div>
                            
                        </div>
                    </div>
                </div>
                <div className="price_card">
                    <h3 className="mb_4">Payment Summary</h3>
                    <div className="price_details">
                        <p className="">Product Price (INR)</p>
                        <p className="">10,000.00</p>
                    </div>
                    <div className="price_details">
                        <p className="">Product Price (INR)</p>
                        <p className="">1,000.00</p>
                    </div>
                    <div className="price_details">
                        <p className="">Logistics  Price (INR)</p>
                        <p className="">9,052.00</p>
                    </div>
                    <div className="price_details">
                        <p className="">Logistics  Price (INR)</p>
                        <p className="">1,252.00</p>
                    </div>
                </div>
            </div>

            </div>
        </div>
      </section>

      <Footer />
    </>
  );
}

export default Newcheckout;
