import './pdf.css' ;
import PictureAsPdfIcon from '@mui/icons-material/PictureAsPdf';
import FileDownloadOutlinedIcon from '@mui/icons-material/FileDownloadOutlined';
import MoreVertIcon from '@mui/icons-material/MoreVert';


function Newpdf(props) {
    return ( 
        <div className="pdf-card new-pdf-card">
            <div className="pdf-header new-pdf-header">
                <PictureAsPdfIcon fontSize="large"/>
                <MoreVertIcon className='option-icon' />
            </div>
            <div className="pdf-details new-pdf-details">
                <div>
                    <div className="new-pdf-name">{props.name}</div>
                    <div className="new-pdf-size">{props.size}</div>
                </div>
                <FileDownloadOutlinedIcon className='download-icon' />
            </div>
        </div>
     );
}

export default Newpdf;