import './productdetails.css';
import Navbar from '../Components/Navbar/Navbar';
import SearchIcon from '@mui/icons-material/Search';
import StarIcon from '@mui/icons-material/Star';
import AddIcon from '@mui/icons-material/Add';
import RemoveIcon from '@mui/icons-material/Remove';
import { Link } from 'react-router-dom';
// 
import * as React from 'react';
import Pd from '../Components/Pd/Pd';
import Slider from "../Components/Slider/Slider";

import Cart from '../../../assets/Bag_fill.png';
import Img from '../../../assets/product.png';
import Img2 from '../../../assets/product2.png';
import Imginfo from '../../../assets/Information.svg';
import Footer from '../../landing_page/Footer';
import ProductDesc from '../Components/ProductDesc/ProductDesc';
import { useState } from 'react';
import $ from 'jquery';
import "jquery-ui-dist/jquery-ui";
import {useEffect} from "react";
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select';


function Product_Details() {
    const [size, setAge] = React.useState('');

    const handleChange = (event) => {
        setAge(event.target.value);
    };

    const [value, setValue] = React.useState(0);
    function Add() {
        setValue(value + 1);
    }
    function Sub() {
        setValue(value - 1);
    }
    const [Cost, setCost] = useState(0);


    const getInitialState = () => {
        const val = "0";
        return val;
    };
    const getInitialStateN = () => {
        const valN = "0";
        return valN;
    };

    const [val, setVal] = useState(getInitialState);
    const [valN, setValN] = useState(getInitialStateN);

    const hC = (e) => {
        setVal(e.target.value);
    };
    const hCN = (e) => {
        setValN(e.target.value);
    };


    useEffect ((useEffect)=>{
        $('#a25').click(function(){
            $('.tab1').show();
            $('.tab2').hide();
            $('.tab3').hide();
            $('.dfltCheck').removeClass('dfltCheck');
        });
        $('#a50').click(function(){
            $('.tab1').hide();
            $('.tab2').show();
            $('.tab3').hide();
            $('.dfltCheck').removeClass('dfltCheck');
        });
        $('#a75').click(function(){
            $('.tab1').hide();
            $('.tab2').hide();
            $('.tab3').show();
            $('.dfltCheck').removeClass('dfltCheck');
        });
    },[]);

    return (
        <>
        
            <Navbar />
            <section className="product-details-container">
                <div className="pd-divider">
                    <div className="pd-left">
                        <div><Pd img={Img} img2={Img2} /></div>
                    </div>
                    <div className="pd-right">
                        <div className='pd-right-details'>
                            <div className="product-name">ICFS CM 350VESF Mortar Bonding Chemical</div>


                            <div className="product-rate-avail">
                                <div className='product-availibility'>In Stock</div>
                                <div className="product-rating">
                                    <div className='rating-heading'>Ratings</div>
                                    <div className='stars-div'>
                                        <div className="ratings-stars">
                                            <StarIcon className="start-b" />
                                            <StarIcon className="start-b" />
                                            <StarIcon className="start-b" />
                                            <StarIcon className="start-b" />
                                            <StarIcon className="start-g" />
                                        </div>
                                        <div className="ratings-numbers">140,52</div>
                                    </div>
                                </div>
                            </div>


                            <div className="product-des">
                                <div className="uni-heading">Description</div>
                                <p className="">
                                    Lorem ipsum dolor sit amet consectetur adipisicing elit. Possimus, velit nihil? Laudantium architecto ut in facere corporis. Commodi dolorem accusamus dolore laborum explicabo expedita porro qui consequatur dicta quidem, ab nobis, exercitationem unde aliquid! Corrupti?
                                </p>
                            </div>


                            <div className="product-qty-pincode">
                                <div className="product-size">
                                    <div className="uni-heading">
                                        <span style={{float:"left",marginRight:"20px", marginTop:"10px"}}>Packed Size</span>
                                        <div class="RadioBTNbutton">
                                            <input type="radio" id="a25" name="qnt1" />
                                            <label class="btn btn-default dfltCheck" for="a25" >ML</label>
                                            </div>
                                            <div class="RadioBTNbutton">
                                            <input type="radio" id="a50" name="qnt1" />
                                            <label class="btn btn-default" for="a50">Liter</label>
                                            </div>
                                            <div class="RadioBTNbutton">
                                            <input type="radio" id="a75" name="qnt1" />
                                            <label class="btn btn-default" for="a75">Gallon</label>
                                        </div>
                                        </div>
                                    {/* <div className="size-dropdown">320ml</div> */}
                                    {/* <select style={{clear:"both", float:"left", marginTop:"20px"}} className='size-dropdown' name="" id="">
                                        <option value="320ml">320ml</option>
                                        <option value="500ml">500ml</option>
                                        <option value="750ml">750L</option>
                                        <option value="1000ml">1Gallon</option>
                                    </select> */}
                                    <div className='tab1' style={{clear:"both", float:"left", marginTop:"20px"}}>
                                        <select style={{height:"40px",width:"120px",paddingLeft:"12px",borderRadius:"5px",background:"#EFEFEF",border:"none"}}>
                                            <option value="250">250 ML</option>
                                            <option value="500">500 ML</option>
                                            <option value="750">750 ML</option>
                                        </select>
                                    </div>
                                    <div className='tab2' style={{clear:"both", float:"left", marginTop:"20px"}}>
                                        <select style={{height:"40px",width:"120px",paddingLeft:"12px",borderRadius:"5px",background:"#EFEFEF",border:"none"}}>
                                            <option value="1">1 Liter</option>
                                            <option value="1.5">1.5 Liter</option>
                                            <option value="2">2 Liter</option>
                                        </select>
                                    </div>
                                    <div className='tab3' style={{clear:"both", float:"left", marginTop:"20px"}}>
                                        <select style={{height:"40px",width:"120px",paddingLeft:"12px",borderRadius:"5px",background:"#EFEFEF",border:"none"}}>
                                            <option value="1">1 Gallon</option>
                                            <option value="2">2 Gallon</option>
                                            <option value="3">3 Gallon</option>
                                        </select>
                                    </div>
                                </div>
                                <div className="product-qty">
                                    <div className="uni-heading">Quantity</div>
                                    <div className="product-qty-input">
                                        <button onClick={Sub}><RemoveIcon /></button>
                                        <div className="show-qty">{value < 0 ? 0 : value}</div>
                                        <button onClick={Add}><AddIcon /></button>
                                    </div>
                                </div>
                                <div className="product-pincode">
                                    <div className="uni-heading">Pincode Check</div>
                                    <form className="product-pincode-input">
                                        <input type="text" placeholder='110064' />
                                        <button><SearchIcon /></button>
                                    </form>
                                </div>
                            </div>


                            <div className="table-c table-cNoScroll">
                                <table className="seller-price-table pdt">
                                    <tr className='tablerow-head'>
                                        <th className='table-head'></th>
                                        <th className='table-head'>Seller</th>
                                        <th className='table-head infoic'>Type <img src={Imginfo} />
                                            <div className='infoic-box'>
                                                <h5>Information</h5>
                                                <h4>Exfactory Cost</h4>
                                                <p>The actual price will vary and depends upon multiple factors including the volume purchased and the market price of the day. The actual price will be calculated on the shipping page</p>
                                                <h4>Landed Cost</h4>
                                                <p>The actual Landed cost will vary and depends upon the volume and the logistics partner selected. The actual landed price will be calculated on the shipping page</p>
                                            </div>
                                        </th>
                                        <th className='table-head'>Cost</th>
                                    </tr>
                                    <tr className='tablerow-data'>
                                        <td className='table-data inp-td'><input type="checkbox" /></td>
                                        <td className='table-data'>Burhani Trader</td>
                                        <td className='table-data'>
                                            <div class="mb-3">
                                                <select onChange={hC} class="type-selector" name="" id="">
                                                    <option  value="0">Select Type</option>
                                                    <option value="50">Exfactory cost</option>
                                                    <option value="100">Landed Cost</option>
                                                </select>
                                            </div>
                                        </td>
                                        <td className='table-data'>{val}</td>
                                    </tr>
                                    <tr className='tablerow-data'>
                                        <td className='table-data inp-td'><input type="checkbox" /></td>
                                        <td className='table-data'>Shakti Trader</td>
                                        <td className='table-data'>
                                            <div class="mb-3">
                                                <select onChange={hCN} class="type-selector" name="" id="">
                                                    <option  value="0">Select Type</option>
                                                    <option value="50">Exfactory cost</option>
                                                    <option value="100">Landed Cost</option>
                                                </select>
                                            </div>
                                        </td>
                                        <td className='table-data'>{valN}</td>
                                    </tr>
                                </table>
                            </div>



                            <div className="price-desc">
                                <div className="price-heading">Price</div>
                                <div className="ite-price-container">
                                    <span className="price old-price">60</span>
                                    <span className="price new-price">60</span>
                                    <span className="price-span">Per Piece</span>
                                </div>
                                <div className="discount-percentage">(10% Off)</div>
                                <div className="price-btn-container df aic">
                                    <Link to='/cart' className="price-btn">Buy Now</Link>
                                    <Link to='/rfqbasic' className="price-btn">RFQ</Link>
                                </div>
                            </div>


                            <div className="logistics-table-container">
                                <table className='logistics-table'>
                                    <thead>
                                        <tr className='logistics-headrow'>
                                            <th className='logistics-head'></th>
                                            <th className='logistics-head'>Logistics Provider</th>
                                            <th className='logistics-head'>Logistics Cost(INR)</th>
                                            <th className='logistics-head'>Delivery TAT(Day)</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr className='logistics-datarow'>
                                            <td className='logistics-data log-inpu'><input type="checkbox" /></td>
                                            <td className='logistics-data '>Logistics Partner</td>
                                            <td className='logistics-data '>12000</td>
                                            <td className='logistics-data '>2-4</td>
                                        </tr>
                                        <tr className='logistics-datarow'>
                                            <td className='logistics-data log-inpu'><input type="checkbox" /></td>
                                            <td className='logistics-data '>Logistics Partner</td>
                                            <td className='logistics-data '>12000</td>
                                            <td className='logistics-data '>2-4</td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>


                        </div>
                    </div>
                </div>
            </section>
            <section>
                <ProductDesc />
            </section>
            <section>
                <h2 className='h2-heading'>Related Products</h2>
                <Slider />
            </section>
            <Footer />
        </>
    );
}

export default Product_Details;